import * as React from 'react';
import { graphql, navigate } from 'gatsby';
import { defineCustomElements as deckDeckGoHighlightElement } from '@deckdeckgo/highlight-code/dist/loader';

import Layout from '../components/layout';
import Seo from '../components/seo';
import BlogPostContent from '../components/blog-post-content';
import { Container, PostIcon, PostDate } from '../style/blogPoststyle';
import { PostItemHeader, PostTag } from '../components/post-item/style';

function BlogPostTemplate({ data: { previous, next, site, markdownRemark: post }, location }) {
 const siteTitle = site.siteMetadata?.title || 'Title';

 deckDeckGoHighlightElement();

 return (
  <Layout location={location} title={siteTitle}>
   {/* <TableOfContents dangerouslySetInnerHTML={{ __html: post.tableOfContents }} itemProp="articleBody" /> */}
   <Container>
    <header>
     <PostIcon>{post.frontmatter.icon}</PostIcon>
     <PostItemHeader>
      {post.frontmatter.tags?.map((tag) => (
       <PostTag
        key={tag}
        onClick={() => {
         navigate(`/tags/${tag}`);
        }}
       >
        {tag}
       </PostTag>
      ))}
     </PostItemHeader>
     <h3 itemProp="headline">{post.frontmatter.title}</h3>
     <PostDate>{post.frontmatter.date}</PostDate>
     <hr />
     <br />
    </header>
    <BlogPostContent previous={previous} next={next} post={post} />
   </Container>
  </Layout>
 );
}

export function Head({ data: { markdownRemark: post } }) {
 return <Seo title={post.frontmatter.title} description={post.frontmatter.description || post.excerpt} />;
}

export default BlogPostTemplate;

export const pageQuery = graphql`
 query BlogPostBySlug($id: String, $previousPostId: String, $nextPostId: String) {
  site {
   siteMetadata {
    title
   }
  }
  markdownRemark(id: { eq: $id }) {
   id
   excerpt(pruneLength: 80)
   html
   tableOfContents
   frontmatter {
    title
    date(formatString: "MMMM DD, YYYY")
    description
    category
    icon
    tags
   }
  }
  previous: markdownRemark(id: { eq: $previousPostId }) {
   fields {
    slug
   }
   frontmatter {
    title
   }
  }
  next: markdownRemark(id: { eq: $nextPostId }) {
   fields {
    slug
   }
   frontmatter {
    title
   }
  }
 }
`;
